.popup-fq
{
    width: 80%;
    height: 80vh;
    position: fixed;
    top: 55%;
    left: 50%;
    background-color: white;
    transform: translate(-50%,-50%);
    z-index: 10000;
    transition: 1s !important;
    border-radius: 20px;
}
.overlayer-fq
{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    filter: blur(5px) !important;
    background-color: rgba(102, 102, 102, 0.479);
    z-index: 1000;
    transition: 1s !important;
}